.page {
  position: relative;

  scroll-behavior: smooth;

  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      margin-bottom: 48px;

      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      text-align: center;

      color: $dark-main-accent;

      @include onTablet {
        margin-bottom: 64px;

        font-size: 40px;
        line-height: 40px;
      }

      @include onDesktop {
        font-size: 48px;
        line-height: 48px;
      }
    }
  }

  //&__block-appearence {
  //
  //}
}
